
export const collisionGOAL = (c, l) => {
  const { x1, x2, y: Y} = l;
  const { position: { x, y } } = c;
  return y < Y && x > x1 && x < x2;
};

export const collisionCC = (c1, c2) => {
	const { position: {x: x1, y: y1 }, radius: r1 } = c1;
	const { position: {x: x2, y: y2 }, radius: r2 } = c2;
  return (r1 + r2) > Math.sqrt((x2 - x1) * (x2 - x1) + (y2 - y1) * (y2 - y1));
};

export const contain = (body, SET) => {
    const { position: {x, y}, radius } = body;
    return !(false
        || x - radius < SET.x 
        || (((y - radius) < SET.y) && (x < SET.goal[0] || x > SET.goal[1]))
        || x + radius > SET.x + SET.width
        // || y + radius > SET.y + SET.height
      );
};


