export default (keyCode) => {
	let key = {};
	key.code = keyCode;
	key.release = undefined;

	key.upHandler = event => {
		event.preventDefault();
		if (event.keyCode === key.code) {
			key.release && key.release();
		}
	};
  
	window.addEventListener(
		"keyup", key.upHandler.bind(key), false
	);

	return key;
}


