import { Sprite, Graphics } from 'pixi.js';

const getRandomDir = () => Math.random() >= 0.5 ? 1 : -1;

export default (x, y, radius, res) => {
    const body = new Graphics();
    body.beginFill(0xFFFFFF);
    body.drawCircle(0, 0, radius);
    body.endFill();
    
    body.radius = radius;
    body.x = x;
    body.y = y;

    body.isMoving = true;
    body.isControlling = true;

    let direction = getRandomDir();
    const sprite = new Sprite(res.ball.texture);
    sprite.scale.set(0.08);
    sprite.anchor.set(0.5);

    sprite.x = x;
    sprite.y = y;

    sprite.mask = body;

    let horSpeed = 1.5;
    let verSpeed = 0.5;
    let kickSpeed = 4;

    body.turnLeft = () => {
        if (!body.isControlling) return;
        direction = -1;
    }

    body.turnRight = () => {
        if (!body.isControlling) return;
        direction = 1;
    }

    body.stop = () => {
        body.isMoving = false;
    }

    body.kick = () => { 
        res.kick.sound.play();
        body.isControlling = false;
        direction = 0; 
        verSpeed = kickSpeed 
    } 

    body.move = (time) => {
        if (!body.isMoving) {
            return false;
        }

        const X = body.position.x + horSpeed * direction * time;
        const Y =  body.position.y - verSpeed * time;
        
        body.position.set(X, Y);
        sprite.position.set(X, Y);
    }

    body.reset = () => {
        body.isMoving = true;
        body.isControlling = true;
        body.position.set(x, y);
        verSpeed = 0.5;
        horSpeed = 1.5;
        direction = getRandomDir();
    }

	return { ball: body, sprite };
}


