import Player from './player';

const getRandomDir = () => Math.random() >= 0.5 ? 1 : -1;
const getRandomX = (x1, x2) => Math.random() * (x2 - x1) + x1;

export default (y, x1, x2, radius) => {
    const left = x1;
    const right = x2;
    const x = getRandomX(left, right);
    const player = Player(x, y, radius);

    let dir = getRandomDir();

    player.move = (time, speed=0.6) => {
        let newX = player.position.x + speed * dir * time;

        if (newX > right) {
            newX = right;
            dir = -dir;
        }

        if (newX < left) {
            newX = left;
            dir = -dir;
        }

        player.position.set(newX, y);
    }

    return player;
}


