import { Graphics } from 'pixi.js';

export default (x, y, radius) => {
    let body = new Graphics();
    body.beginFill(0xFFFFFF);
    body.drawCircle(0, 0, radius);
    body.endFill();
    
    body.radius = radius;
    body.x = x;
    body.y = y;

	return body;
}


