import * as PIXI from 'pixi.js';

class Line extends PIXI.Graphics {
    constructor(points, lineSize, lineColor) {
        super();
        const s = this.lineWidth = lineSize || 1;
        const c = this.lineColor = lineColor || " WHITE_COLOR";
        this.points = points;
        this.lineStyle(s, c)
        this.moveTo(points[0], points[1]);
        this.lineTo(points[2], points[3]);
    }
}

const WHITE_COLOR = 0xFFFFFF;
const GRASS_OUTSIDE = 0x5AB420;
const GRASS_COLOR_1 = 0x68B621;
const OUTSIDE_COLOR = 0xFFFFFF;
// const OUTSIDE_COLOR = 0xEEEEEE;

const setP = (px) => {
    return {
        0: 0*px,
        '0.5': 0.5*px,
        1: 1*px,
        '1.4': 1.4*px,
        '1.5': 1.5*px,
        '1.6': 1.6*px,
        '1.7': 1.7*px,
        '1.8': 1.8*px,
        '1.9': 1.9*px,
        2: 2*px,
        '2.5': 2.5*px,
        3: 3*px,
        '3.5': 3.5*px,
        4: 4*px,
        '4.5': 4.5*px,
        '4.6': 4.6*px,
        5: 5*px,
        '5.2': 5.2*px,
        '5.4': 5.4*px,
        '5.6': 5.6*px,
        '5.8': 5.8*px,
        6: 6*px,
        '6.2': 6.2*px,
        '6.4': 6.4*px,
        '6.5': 6.5*px,
        '6.6': 6.6*px,
        '6.8': 6.8*px,
        7: 7*px,
        '7.2': 7.2*px,
        '7.4': 7.4*px,
        '7.5': 7.5*px,
        8: 8*px,
        '8.5': 8.5*px,
        9: 9*px,
        10: 10*px,
        '10.5': 10.5*px,
        11: 11*px,
        '11.5': 11.5*px,
        12: 12*px
    };
}

export const Goal = (app, WIDTH, goal) => {
    const px = WIDTH / 12;
    const P = setP(px);
    const mpx = P[1] / 25;
    const y1 = goal[2] - WIDTH / 24;

    let x = goal[0];
    let y = goal[2];

    while (x <= goal[1]) {
        app.stage.addChild(new Line([x, P['1.4'], x, P[2]],mpx, WHITE_COLOR));
        x += WIDTH / 60;
    }

    while (y > y1) {
        app.stage.addChild(new Line([goal[0],y - WIDTH / 60,goal[1],y - WIDTH / 60],mpx, WHITE_COLOR));
        y -= WIDTH / 60;
    }
}

export default (app, WIDTH, HEIGHT) => {
    const px = WIDTH / 12;
    const P = setP(px);
    const mpx = P[1] / 25;
    // FIELD
    const field = new PIXI.Graphics();
    field.beginFill(GRASS_COLOR_1);
    field.drawRect(0, 0, P[8], P[10]);
    field.endFill();
    field.x = P[2];
    field.y = P[2];
    app.stage.addChild(field);

    // FIELD LINES
    app.stage.addChild(new Line([P[2],P['4.5'],P[10],P['4.5']],P[1],GRASS_OUTSIDE));
    app.stage.addChild(new Line([P[2],P['6.5'],P[10],P['6.5']],P[1],GRASS_OUTSIDE));
    app.stage.addChild(new Line([P[2],P['8.5'],P[10],P['8.5']],P[1],GRASS_OUTSIDE));
    app.stage.addChild(new Line([P[2],P['10.5'],P[10],P['10.5']],P[1],GRASS_OUTSIDE));
    
    // CENTRE CIRCLE
    const cntrCircle = new PIXI.Graphics();
    cntrCircle.lineStyle(mpx,  WHITE_COLOR, mpx);
    cntrCircle.drawCircle(0, 0, P['1.4']);
    cntrCircle.endFill();
    cntrCircle.x = P[6];
    cntrCircle.y = P[11];
    app.stage.addChild(cntrCircle);
    app.stage.addChild(new Line([P[2],P[11],P[10],P[11]],mpx, WHITE_COLOR));

    // GOAL ZONE CIRCLE
    const goalCircle = new PIXI.Graphics();
    goalCircle.lineStyle(mpx,  WHITE_COLOR, mpx);
    goalCircle.drawCircle(0, 0, P[1] * 1.2);
    goalCircle.endFill();
    goalCircle.x = P[6];
    goalCircle.y = P[3];
    app.stage.addChild(goalCircle);

    // GOAL ZONE RECT
    const goalZone = new PIXI.Graphics();
    goalZone.beginFill(GRASS_COLOR_1);
    goalZone.drawRect(0, 0, P[4], P['1.8']);
    goalZone.endFill();
    goalZone.x = P[4];
    goalZone.y = P[2];
    app.stage.addChild(goalZone);
    app.stage.addChild(new Line([P[2],P['2.5'],P[10],P['2.5']],P[1],GRASS_OUTSIDE));
    
    // TABLE
    const table = new PIXI.Graphics();
    table.beginFill(OUTSIDE_COLOR);
    table.drawRect(0, 0, P[12], P[4]);
    table.endFill();
    table.x = P[0];
    table.y = P[12];
    app.stage.addChild(table);

    // MARKUP
    const markup1 = new PIXI.Graphics();
    markup1.lineStyle(mpx,  WHITE_COLOR, mpx);
    markup1.drawRect(0, 0, P[8], P[10]);
    markup1.endFill();
    markup1.x = P[2];
    markup1.y = P[2];
    app.stage.addChild(markup1);

    const markup2 = new PIXI.Graphics();
    markup2.lineStyle(mpx,  WHITE_COLOR, mpx);
    markup2.drawRect(0, 0, P[4], P['1.8']);
    markup2.endFill();
    markup2.x = P[4];
    markup2.y = P[2];
    app.stage.addChild(markup2);

    //AD: LEFT, RIGHT, TOP
    app.stage.addChild(new Line([P['0.5'],P[1],P['0.5'],P[12]],P[1],OUTSIDE_COLOR));
    app.stage.addChild(new Line([P['11.5'],P[1],P['11.5'],P[12]],P[1],OUTSIDE_COLOR));
    app.stage.addChild(new Line([P[0],P['0.5'],P[12],P['0.5']],P[1],OUTSIDE_COLOR));

    // GRASS AROUND THE FIELD: LEFT, RIGHT, TOP
    app.stage.addChild(new Line([P['1.5'],P[1],P['1.5'],P[12]],P[1],GRASS_COLOR_1));
    app.stage.addChild(new Line([P['10.5'],P[1],P['10.5'],P[12]],P[1],GRASS_COLOR_1));
    app.stage.addChild(new Line([P[1],P['1.5'],P[11],P['1.5']],P[1],GRASS_COLOR_1));

    

    const goalCircle1 = new PIXI.Graphics();
    goalCircle1.beginFill( WHITE_COLOR);
    goalCircle1.drawCircle(0, 0, P[1] * 0.05);
    goalCircle1.endFill();
    goalCircle1.x = P[6];
    goalCircle1.y = P[3];
    app.stage.addChild(goalCircle1);

    const cntrCircle1 = new PIXI.Graphics();
    cntrCircle1.beginFill( WHITE_COLOR);
    cntrCircle1.drawCircle(0, 0, P[1] * 0.1);
    cntrCircle1.endFill();
    cntrCircle1.x = P[6];
    cntrCircle1.y = P[11];
    app.stage.addChild(cntrCircle1);

    return {
        WIDTH: WIDTH,
        HEIGHT: HEIGHT,
        x: P[2],
        y: P[2],
        width: P[8],
        height: P[12],
        startX: P[6],
        startY: P[11],
        goal: [P['4.6'], P['7.4'], P[2]],
        lines: [P['2.5'], P[4], P['6.5'], P[9]],
        ball: P[1] / 4,
        player: P[1] / 3,
        post: P[1] / 9,
        team: P[12] + P[1],
        time: P[12] + P[2],
        status: P[12] + P[3],
        mpx,
    }
}



