import React, { Component } from 'react';
import * as PIXI from 'pixi.js';
import keyboard from './helpers/keyboard';
import { collisionGOAL, collisionCC, contain } from './helpers/collision';
import 'pixi-sound';

import Ball from './actors/ball';
import Post from './actors/post';
import Row from './actors/row';
import Field, { Goal } from './actors/field';
import Game from './Game';

class App extends Component {
	componentDidMount() {
		PIXI.loader
			.add('kick', 'sounds/kick.mp3')
			.add('ws', 'sounds/ws.mp3')
			.add('wse', 'sounds/wse.mp3')
			.add('post', 'sounds/post.mp3')
			.add('goal', 'sounds/goal.mp3')
			.add('ball', 'ball.jpg')
			.load(this.play);
	}
	
	play(loader, resources) {
		const HEIGHT = window.innerHeight;
		const WIDTH = HEIGHT / 4 * 3;
		const app = new PIXI.Application({
			width: WIDTH,
			height: HEIGHT,
			antialias: true,
			transparent: false,
			resolution: 1,
		});

		document.getElementById('container').appendChild(app.view);

		const SET = Field(app, WIDTH, HEIGHT);
		
		const game = new Game({
			lang: 'en',
			level: 1,
			team1: 'PIV',
			team2: 'WWW',
			resources,
		}, SET);

		game.setControls(app);

		const goal = {x1: SET.goal[0], x2: SET.goal[1], y: SET.goal[2]};
		const { ball, sprite } = Ball(SET.startX, SET.startY, SET.ball, resources);
		const post1 = Post(SET.goal[0], SET.goal[2], SET.post);
		const post2 = Post(SET.goal[1], SET.goal[2], SET.post);
		const team = [
			...Row(SET.x, SET.width, SET.lines[0], 1, SET.player),
			...Row(SET.x, SET.width, SET.lines[1], 2, SET.player),
			...Row(SET.x, SET.width, SET.lines[2], 3, SET.player),
			...Row(SET.x, SET.width, SET.lines[3], 2, SET.player),
		];

		app.stage.addChild(post1);
		app.stage.addChild(post2);
		Goal(app, WIDTH, SET.goal);

		app.stage.addChild(sprite);
		app.stage.addChild(ball);

		team.forEach((rival) => {
			app.stage.addChild(rival);
		});


		const left  = keyboard(37);
		const right = keyboard(39);
		const space = keyboard(32);
		const enter = keyboard(13);

		left.release = () => ball.turnLeft();
		right.release = () => ball.turnRight();
		space.release = () => ball.kick();
		enter.release = () => {
			ball.reset();
			game.continue();
		};

		app.ticker.add((time) => {

			if (game.status > 0) {
				return;
			}

			game.updateTime(time)

			team.forEach((rival) => {
				if (collisionCC(ball, rival)) {
					game.loss();
				}

				rival.move(time * game.level * SET.mpx);
			});

			if (!contain(ball, SET)) {
				game.out();
			}

			if (collisionGOAL(ball, goal)) {
				game.goal(1);
			}

			if (collisionCC(post1, ball) || collisionCC(post2, ball)) {
				game.post();
			}

			ball.move(time * game.level * SET.mpx);
		});
	}

	render() {
		return (
			<div className="App">
				<div id="sidebar" />
				<div id="menu">
					<h1>20f8tball</h1>
					<p>{"<- turn left"}</p>
					<p>{"-> turn right"}</p>
					<p>{"[ space ] kick"}</p>
				</div>
				<div id="container" />
			</div>
		);

		return (
			<div className="App">
				<div id="sidebar" />
				<div id="menu">
					<h1>20f8tball</h1>
					<h5></h5>
					<p></p>
					<p></p>
					<p></p>
					<h5></h5>
					<p>0</p>
				</div>
				<div id="container" />
			</div>
		);
	}
}

export default App;



