import * as PIXI from 'pixi.js';
import codes from './consts/status';
import { txtSet, txtSet2, txtSet3 } from './consts/settings';

const GAME = function(opt={}, SET) {
    this.lang = opt.lang;
    this.time = 0;
    this.status = 0;
    this.controls = {}; 

    this.res = opt.resources;
    this.controls.timer = new PIXI.Text(this.time, txtSet);
    this.controls.timer.position.set(SET.WIDTH / 2, SET.time);
    this.controls.timer.anchor.set(0.5);
    
    this.controls.status = new PIXI.Text(codes[this.status][this.lang], txtSet3);
    this.controls.status.position.set(SET.WIDTH / 2, SET.status);
    this.controls.status.anchor.set(0.5);

    this.level = opt.level;

    if (!!opt.team1) {
        this.team1 = {
            name: opt.team1, score: 0,
        };

        this.controls.score = new PIXI.Text(`${opt.team1} 0`, txtSet2);

    }

    if (!!opt.team2) {
        this.team2 = {
            name: opt.team2, score: 0,
        };

        this.controls.score = new PIXI.Text(`${opt.team1} 0 - 0 ${opt.team2}`, txtSet2);
    }

    this.controls.score.anchor.set(0.5);
    this.controls.score.position.set(SET.WIDTH / 2, SET.team);
    
};

GAME.prototype.setControls = function(app) {
    app.stage.addChild(this.controls.timer);
    app.stage.addChild(this.controls.status);
    app.stage.addChild(this.controls.score);
}

GAME.prototype.updateStatus = function(n) {
    this.status = n;
    this.controls.status.text = codes[this.status][this.lang];

    if ([0,2,6].indexOf(n) !== -1) {
        this.res.ws.sound.play();
    }

    if ([4].indexOf(n) !== -1) {
        this.res.post.sound.play();
    }

    if ([5].indexOf(n) !== -1) {
        this.res.wse.sound.play();
    }

    if ([3].indexOf(n) !== -1) {
        this.res.goal.sound.play();
        this.res.ws.sound.play();
    }
}

GAME.prototype.updateTime = function(time) {
    this.time += time * 0.03;
    const newTime = this.time.toFixed(2).split('.');
    this.controls.timer.text = `${newTime[0]}:${Math.round(newTime[1]/100*60)}`;
    if (this.time > 90) {
        this.updateStatus(5);
    }
}

GAME.prototype.continue = function() {
    this.updateStatus(0);
}

GAME.prototype.goal = function(n) {
    if (n === 1) {
        this.team1.score = this.team1.score + 1;
    }

    if (n === 2) {
        this.team2.score = this.team2.score + 1;
    }

    this.controls.score.text = `${this.team1.name} ${this.team1.score}`;
   
    if (!!this.team2) {
        this.controls.score.text = `${this.team1.name} ${this.team1.score} - ${this.team2.score} ${this.team2.name}`;
    }

    this.updateStatus(3);
}

GAME.prototype.loss = function() {
    this.updateStatus(1);
}

GAME.prototype.out = function() {
    this.updateStatus(2);
}

GAME.prototype.post = function() {
    this.updateStatus(4);
}

export default GAME;




