import Rival from './rival';

export default (x, fWidth, y, inRow, radius) => {
    if (inRow === 1) {
        const delta = fWidth / 3;
        const x11 = x + delta + fWidth / 15;
        const x12 = x + 2 * delta - fWidth / 15;
        return [Rival(y, x11, x12, radius)];
    }

    if (inRow === 2) {
        const delta = fWidth / 2;
        const x11 = x + 0 + fWidth / 15;
        const x12 = x + delta - fWidth / 15;
        const x21 = x + delta + fWidth / 15;
        const x22 = x + fWidth - fWidth / 15;
        return [Rival(y, x11, x12, radius), Rival(y, x21, x22, radius)];
    }

    if (inRow === 3) {
        const delta = fWidth / 3;
        const x11 = x + 0 + fWidth / 15;
        const x12 = x + delta - fWidth / 15;
        const x21 = x + delta + fWidth / 15;
        const x22 = x + 2 * delta - fWidth / 15;
        const x31 = x + 2 * delta + fWidth / 15;
        const x32 = x + fWidth - fWidth / 15;
        return [Rival(y, x11, x12, radius), Rival(y, x21, x22, radius), Rival(y, x31, x32, radius)];
    }
}



